







































































import * as echarts from '../../components/echarts/echarts.min.js';
import mpvueEcharts from '../../../components/mpvue-echarts/src/echarts.vue';
import {
	IsDate
} from '../../../common/util.js';

function getDay() {
	let time = new Date();
	// let date = time.getFullYear() + '-' + time.getMonth();
	let date = '2018-3'
	let d = 31;
	let arr = [];
	for (let i = 28; i <= 32; i++) {
		if (!IsDate(date + '-' + i)) {
			d = i - 1;
			break;
		}
	}
	for (let i = 1; i <= d; i++) {
		arr.push(i);
	}
	// console.log(arr)
	return arr;
}
let xAxisValuewWeek = ["周一", "周二", "周三", "周四", "周五", "周六", "周日"];
let xAxisValueMonth = ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"];
let xAxisValueDay = getDay();
let monthDataZoom = [{
		show: false,
		start: 0,
		end: 30
	},
	{
		type: 'inside',
		start: 0,
		end: 10
	}
]
let itemStyleNormalColor = function(params) { //自定义颜色
	var colorList = [
		'#5e7e54', '#e44f2f', '#81b6b2', '#eba422', '#5e7e54',
		'#e44f2f', '#81b6b2'
	];
	return colorList[params.dataIndex]
}

function getBarOption(xAxisData, DataZoom, seriesData) {
	return {
		animation: false,
		backgroundColor: '#F8F8F8',
		dataZoom: DataZoom,
		xAxis: {
			axisLine: { //坐标轴轴线相关设置
				show: false
			},
			axisTick: { //坐标轴刻度相关设置
				show: false
			},
			splitLine: { //坐标轴在 grid 区域中的分隔线
				show: false
			},
			data: xAxisData
		},
		yAxis: {
			show: false,
		},
		series: [{
			itemStyle: {
				normal: {
					color: itemStyleNormalColor,
					label: {
						show: true,
						position: 'top',
						textStyle: {
							color: 'white'
						}
					}
				}
			},
			name: '销量',
			type: 'bar',
			data: [{
				value: '5'
			}, {
				value: '20'
			}, {
				value: '36'
			}, {
				value: '10'
			}, {
				value: '15'
			}, {
				value: '20'
			}, {
				value: '20'
			}]
		}]
	}
}
export default {
	data() {
		return {
			barInitArr: ['day', 'month', 'year'],
			barInitState: 'day',
			backgroundColor: "red",
			echarts,
			navList: [{
				type: 'day',
				text: '每日统计',
				color: 'blue'
			}, {
				type: 'month',
				text: '按月统计',
				color: 'yellow'
			}, {
				type: 'year',
				text: '按年统计',
				color: 'red'
			}],
			activeIndex: 0,
			barInit: {
				day: function(canvas, width, height) {
					let barChart = echarts.init(canvas, null, {
						width: width,
						height: height
					})
					canvas.setChart(barChart)
					barChart.setOption(getBarOption(xAxisValuewWeek, null))
					return barChart
				},
				month: function(canvas, width, height) {
					let barChart = echarts.init(canvas, null, {
						width: width,
						height: height
					})
					canvas.setChart(barChart)
					barChart.setOption(getBarOption(xAxisValueDay, monthDataZoom))
					return barChart
				},
				year: function(canvas, width, height) {
					let barChart = echarts.init(canvas, null, {
						width: width,
						height: height
					})
					canvas.setChart(barChart)
					barChart.setOption(getBarOption(xAxisValueMonth, monthDataZoom))
					return barChart
				}
			}
		}
	},
	methods: {
		changeBarInit(item, index) {
			if (item.type == 'day') {
				this.barInitState = item.type;
			}
			if (item.type == 'month') {
				this.barInitState = item.type;
			}
			if (item.type == 'year') {
				this.barInitState = item.type;
			}
			this.backgroundColor = item.color;
			this.activeIndex = index;
		}
	},
	components: {
		mpvueEcharts
	}
}



